import { useCallback, useMemo } from "react"
import { useLocation } from "react-router"
import { useGetKanbans, useReorderKanban } from "../../hooks/kanban"
import { useGetProject } from "../../hooks/project"
import { useGetTask, useReorderTask, useGetTasks } from "../../hooks/task"
import { useUrlQueryParam } from "../../utils/url"
import { DropResult } from 'react-beautiful-dnd'

export const useProjectIdInUrl = () => {
  const { pathname } = useLocation()
  const id = pathname.match(/projects\/(\d+)/)?.[1]
  return Number(id)
}

export const useProjectInUrl = () => useGetProject(useProjectIdInUrl())

export const useKanbanSearchParams = () => ({ projectId: useProjectIdInUrl() })
export const useKanbanQueryKey = () => ['kanbans', useKanbanSearchParams()]


export const useTaskSearchParams = () => { 
  const projectId = useProjectIdInUrl()
  const [param, setParam] = useUrlQueryParam([
    'name',
    'typeId',
    'processorId',
    'tagId'
  ])

  // const debounceName = useDebounce({name: param.name}, 200).name 

  return [
    useMemo(() => ({ 
      projectId,
      processorId: Number(param.processorId) || undefined,
      typeId: Number(param.typeId) || undefined,
      tagId: Number(param.tagId) || undefined,
      name: param.name
    }), [projectId, param]),
    setParam
  ] as const
}
export const useTaskQueryKey = () => ['tasks', useTaskSearchParams()[0]]

export const useTaskModal = () => {
  //{ editingTaskId, isCreateTask }
  const [ TaskState, setTaskState ] = useUrlQueryParam(
    ['editingTaskId', 'isCreateTask', 'kanbanId']
  )
  const { isFetching, data: editingTaskData } = useGetTask(Number(TaskState.editingTaskId))
  const startEdit = useCallback((id: number) => {
    setTaskState({
      ...TaskState,
      editingTaskId: id
    })
  }, [setTaskState, TaskState])

  const startCreate = useCallback((kanbanId: number) => {
    setTaskState({
      ...TaskState,
      kanbanId,
      isCreateTask: true
    })
  }, [setTaskState, TaskState])

  const close = useCallback(() => {
    setTaskState({
      ...TaskState,
      editingTaskId: undefined,
      isCreateTask: undefined,
      kanbanId: undefined
    })
  }, [setTaskState, TaskState])


  return {
    isOpen: !!TaskState.editingTaskId || !!TaskState.isCreateTask,
    editingTaskId: TaskState.editingTaskId,
    kanbanId: TaskState.kanbanId,
    editingTaskData,
    isFetching,
    startEdit,
    startCreate,
    close
  } as const
}

export const useDragEnd = () => {
  const { data: kanbans } = useGetKanbans(useKanbanSearchParams())
  const { mutate: reorderKanban } = useReorderKanban(useKanbanQueryKey())
  const { data: allTasks = [] } = useGetTasks(useTaskSearchParams()[0])
  const { mutate: reorderTask } = useReorderTask(useTaskQueryKey())

  return useCallback(({ source, destination, type }: DropResult) => {
    if (!destination) return
    if (type === 'COLUMN') {
      const fromId = kanbans?.[source.index].id
      const toId = kanbans?.[destination.index].id

      if (!fromId || !toId || fromId === toId) return

      const type = destination.index > source.index ? 'after' : 'before'
      reorderKanban({ fromId, referenceId: toId, type})
    }

    if (type === 'ROW') {
      const fromKanbanId = +source.droppableId
      const toKanbanId = +destination.droppableId
      // if (fromKanbanId !== toKanbanId) return

      const fromTask = allTasks?.filter(task => task.kanbanId === fromKanbanId)[source.index]
      const toTask = allTasks?.filter(task => task.kanbanId === toKanbanId)[destination.index]


      if (fromTask?.id === toTask?.id) return

      reorderTask({
        fromId: fromTask?.id,
        referenceId: toTask?.id,
        type: destination.index > source.index ? 'after' : 'before',
        fromKanbanId,
        toKanbanId
      })
      
    }
    
  },[kanbans, reorderKanban, allTasks, reorderTask])
}