import styled from "@emotion/styled"
import { Form, Input, Modal, Spin } from "antd"
import { useForm } from "antd/lib/form/Form"
import { useEffect } from "react"
import { ErrorBox } from "../../components/lib"
import { EpicSelect, TaskTypeSelect } from "../../components/select"
import { UserSelect } from "../../components/select"
import { useAddTask, useEditTask } from "../../hooks/task"
import { useProjectIdInUrl, useTaskModal, useTaskQueryKey } from "./utils"

export const TaskModal = () => {
  const { editingTaskData, kanbanId, close, isOpen, isFetching} = useTaskModal()
  const [form] = useForm()
  const projectId = useProjectIdInUrl()
  const isEditing = !!editingTaskData
  const useMutateTask = isEditing ? useEditTask : useAddTask
  const { mutateAsync, error, isLoading: isMutateLoading } = useMutateTask(useTaskQueryKey())


  const closeHandler = () => {
    form.resetFields()
    close()
  }

  const onOk = async () => {
    try {
      const values = await form.validateFields();
      let params = {
        ...editingTaskData,
        ...values,
      }
  
      if (!isEditing) {
        params = { ...params, kanbanId, projectId}
      }
  
      await mutateAsync(params)
      closeHandler()
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  }

  useEffect(() => {
    form.setFieldsValue(editingTaskData)
  }, [editingTaskData, form])
  return (
      <Modal
        onOk={onOk}
        onCancel={closeHandler}
        okText="确认"
        cancelText="取消"
        title={editingTaskData ? '编辑任务' : '新建任务'}
        confirmLoading={isMutateLoading}
        visible={isOpen}
        closable={false}
        forceRender={true}
      >
       <SpinWrapper style={{display: isFetching ? 'block' : 'none'}}><Loading size={'large'}/></SpinWrapper> 
        <div style={{display: isFetching ? 'none' : 'block'}}>
        <ErrorBox error={error} />
        <Form {...layout} form={form}>
          <Form.Item 
            rules={[{required: true, message: '请输入任务名'}]} 
            label="任务名" 
            name={'name'}>
            <Input placeholder="任务名"/>
          </Form.Item>
          <Form.Item
            label="经办人"
            name={'processorId'}
            rules={[{ required: true, message: '请输入经办人'}]}>
            <UserSelect defaultOptionName="经办人"/>
          </Form.Item>
          <Form.Item
            label="任务组"
            name={'epicId'}
            rules={[{required: true, message: '请输入任务组'}]}>
            <EpicSelect defaultOptionName="任务组"/>
          </Form.Item>
          <Form.Item
            label="类型"
            name={'typeId'}
            rules={[{required: true, message: '请输入类型'}]}>
            <TaskTypeSelect defaultOptionName="类型"/>
          </Form.Item>
        </Form>
        </div>
      </Modal>
  )
}

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 }
}

const Loading = styled(Spin)`
  display: flex;
  align-items: center;
  justify-content: center;
`

const SpinWrapper = styled.div`
  width: 100%;
  height: 100%;
`