import React from "react"
import { useGetEpics } from "../hooks/epic"
import { useTaskTypes } from "../hooks/task-type"
import { useEpicSearchParams } from "../screens/epic/utils"
import { IdSelect } from "./id-select"
import { useUsers } from "../hooks/use-users"

export const TaskTypeSelect = (props: React.ComponentProps<typeof IdSelect>) => {
  const { data: taskTypes } = useTaskTypes()

  return <IdSelect options={ taskTypes } {...props}/>
}

export const EpicSelect = (props: React.ComponentProps<typeof IdSelect>) => {
  const { data } = useGetEpics(useEpicSearchParams())

  return <IdSelect options={ data } {...props}/>
}

export const UserSelect = (props: React.ComponentProps<typeof IdSelect>) => {
  const { data: users } = useUsers()

  return <IdSelect options={ users } {...props}></IdSelect>
}